    computed: {
        options: function() {
            return [
                {
                    ids: this.ids,
                    left: -20,
                    top: -30,
                    width: 20,
                    component: 'Toggle',
                    info: ['Toggle appearance of raw data.'],
                    toggleProp: 'rawPointsActive',
                    optionName: 'Show/Hide:',
                }, // raw points toggle
                {
                    ids: this.ids,
                    left: 0,
                    top: -30,
                    width: 16,
