                    ids: this.ids,
                    left: 0,
                    top: -30,
                    width: 85,
                    component: 'Dropdown',
                    info: ['Select marker shape for data.'],
                    options: this.store.cache.shapes,
                    optionModifer: '',
                    mainProp: 'shape',
                    actions: ['shape'],
                    disableProp: false,
                    optionName: 'Marker:'
